"use client"

import { useState, useEffect } from "react"
import { createClient } from "@/lib/supabase/client"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Package, Loader2, Archive, Trash2 } from 'lucide-react'
import Link from "next/link"
import { toast } from "@/components/ui/use-toast"

interface ArchivedPack {
  id: string
  title: string
  description: string | null
  price: number
  cover_image_url: string | null
  downloads_count: number
  likes_count: number
  created_at: string
  genre: string | null
  archived: boolean
}

interface ArchivedPacksSectionProps {
  userId: string
}

export function ArchivedPacksSection({ userId }: ArchivedPacksSectionProps) {
  const [archivedPacks, setArchivedPacks] = useState<ArchivedPack[]>([])
  const [loading, setLoading] = useState(true)
  const [actionLoading, setActionLoading] = useState<string | null>(null)
  
  const supabase = createClient()

  useEffect(() => {
    loadArchivedPacks()
  }, [userId])

  async function loadArchivedPacks() {
    try {
      setLoading(true)
      
      const { data, error } = await supabase
        .from("packs")
        .select("*")
        .eq("user_id", userId)
        .eq("archived", true)
        .order("created_at", { ascending: false })

      if (error) {
        if (error.message?.includes("column packs.archived does not exist")) {
          console.log("[v0] The 'archived' column doesn't exist yet. Please run migration script 017_add_archived_column.sql")
          setArchivedPacks([])
        } else {
          console.error("[v0] Error loading archived packs:", error)
        }
        return
      }

      setArchivedPacks(data || [])
    } catch (error) {
      console.error("[v0] Error:", error)
    } finally {
      setLoading(false)
    }
  }

  async function handleUnarchive(packId: string) {
    try {
      setActionLoading(packId)
      
      const { error } = await supabase
        .from("packs")
        .update({ archived: false })
        .eq("id", packId)

      if (error) throw error

      toast({
        title: "Pack restaurado",
        description: "El pack volviÃ³ a estar visible en tu perfil",
      })

      // Remove from archived list
      setArchivedPacks(archivedPacks.filter(p => p.id !== packId))
    } catch (error: any) {
      toast({
        title: "Error",
        description: error.message || "No se pudo restaurar el pack",
        variant: "destructive",
      })
    } finally {
      setActionLoading(null)
    }
  }

  async function handleDelete(packId: string) {
    if (!confirm("Â¿EstÃ¡s seguro? Esta acciÃ³n no se puede deshacer.")) {
      return
    }

    try {
      setActionLoading(packId)
      
      const { error } = await supabase
        .from("packs")
        .update({ is_deleted: true })
        .eq("id", packId)

      if (error) throw error

      toast({
        title: "Pack eliminado",
        description: "El pack fue eliminado permanentemente",
      })

      // Remove from archived list
      setArchivedPacks(archivedPacks.filter(p => p.id !== packId))
    } catch (error: any) {
      toast({
        title: "Error",
        description: error.message || "No se pudo eliminar el pack",
        variant: "destructive",
      })
    } finally {
      setActionLoading(null)
    }
  }

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat("es-AR", {
      style: "decimal",
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(price)
  }

  if (loading) {
    return (
      <div className="flex justify-center items-center py-12">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    )
  }

  if (archivedPacks.length === 0) {
    return (
      <Card className="p-12 text-center rounded-3xl border-2 border-dashed border-border">
        <Archive className="h-16 w-16 mx-auto mb-4 text-muted-foreground" />
        <h3 className="text-xl font-bold text-foreground mb-2">No tenÃ©s packs archivados</h3>
        <p className="text-muted-foreground">Los packs archivados aparecerÃ¡n acÃ¡</p>
      </Card>
    )
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {archivedPacks.map((pack) => (
        <Card
          key={pack.id}
          className="overflow-hidden border-border rounded-2xl"
        >
          <div className="relative aspect-square bg-muted overflow-hidden">
            {pack.cover_image_url ? (
              <img
                src={pack.cover_image_url || "/placeholder.svg"}
                alt={pack.title}
                className="w-full h-full object-cover opacity-60"
              />
            ) : (
              <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-primary/20 to-secondary/20">
                <Package className="h-16 w-16 text-muted-foreground" />
              </div>
            )}
            <div className="absolute top-3 right-3">
              <div className="bg-amber-500 text-white px-3 py-1 rounded-full text-xs font-bold flex items-center gap-1">
                <Archive className="h-3 w-3" />
                ARCHIVADO
              </div>
            </div>
          </div>
          <div className="p-5 space-y-3">
            <h3 className="font-bold text-lg text-foreground line-clamp-2">{pack.title}</h3>
            <div className="flex items-center justify-between">
              <div>
                <div className="text-2xl font-black text-foreground">${formatPrice(pack.price)}</div>
                <div className="text-xs text-muted-foreground">ARS</div>
              </div>
              <div className="text-sm text-muted-foreground">
                {pack.downloads_count} ventas
              </div>
            </div>
            <div className="flex gap-2 pt-2">
              <Button
                variant="outline"
                size="sm"
                onClick={() => handleUnarchive(pack.id)}
                disabled={actionLoading === pack.id}
                className="flex-1 rounded-full text-xs"
              >
                {actionLoading === pack.id ? (
                  <Loader2 className="h-3 w-3 animate-spin" />
                ) : (
                  "Restaurar"
                )}
              </Button>
              <Button
                variant="ghost"
                size="sm"
                onClick={() => handleDelete(pack.id)}
                disabled={actionLoading === pack.id}
                className="rounded-full text-xs text-red-600 hover:text-red-600 hover:bg-red-500/10"
              >
                <Trash2 className="h-3 w-3" />
              </Button>
            </div>
          </div>
        </Card>
      ))}
    </div>
  )
}
